import React from "react";
import { useLocation } from "@reach/router";
import Header from "../../../componets/header/Header";
import Footer from "../../../componets/footer/Footer";
import { events } from "../../../data/events";
import { useLanguage } from "../../../context/LanguageContext";
import { getContent } from "../../../content/content";
import "../../../styles/checkout.css";

const FailPage: React.FC = () => {
  const location = useLocation();
  const { lang } = useLanguage();
  const content = getContent(lang).pages.landing.section.eventContent;
  const eventSlug = location.pathname.split("/")[2];
  const eventData = events.find(e => e.id === eventSlug);

  const handleRetry = () => {
    window.location.href = `/event/${eventSlug}/checkout`;
  };

  const handleBackToEvent = () => {
    window.location.href = `/event/${eventSlug}`;
  };

  return (
    <>
      <Header />
      <main className="checkout-page fail">
        <div className="fail-container">
          <div className="fail-content">
            <div className="fail-icon">❌</div>
            <h1 className="fail-title">{content.fail.title}</h1>
            <p className="fail-message">
              {content.fail.message} {eventData?.name || "this event"}.
            </p>
            
            <div className="fail-reasons">
              <h3>{content.fail.reasons.title}</h3>
              <ul>
                {content.fail.reasons.list.map((reason, index) => (
                  <li key={index}>{reason}</li>
                ))}
              </ul>
            </div>

            <div className="fail-actions">
              <button onClick={handleRetry} className="btn-primary">
                {content.fail.actions.retry}
              </button>
              <button onClick={handleBackToEvent} className="btn-secondary">
                {content.fail.actions.backToEvent}
              </button>
            </div>

            <div className="fail-support">
              <p>{content.fail.support.title}</p>
              <p>{content.fail.support.email}</p>
              <p>{content.fail.support.phone}</p>
            </div>
          </div>
        </div>
      </main>
      <Footer />
    </>
  );
};

export default FailPage;